import { Component, Event, EventEmitter, h, Prop } from '@stencil/core';
import { FnIcon } from '../../utils/functional/functional';
import { mergeClasses, parseClassNames } from '../../utils/utils';

@Component({
  tag: 'storm-button',
  styleUrl: 'storm-button.css',
  shadow: false,
})
export class StormButton {
  @Prop() buttonStyle?:
    | 'primary'
    | 'secondary'
    | 'success'
    | 'info'
    | 'warning'
    | 'danger'
    | 'link'
    | 'dark'
    | 'outline-secondary'
    | 'outline-primary'
    | 'outline-success'
    | 'outline-info'
    | 'outline-warning'
    | 'outline-danger'
    | 'outline-dark';
  @Prop() active?: boolean;
  @Prop() disabled?: boolean;
  @Prop() size: '' | 'sm' | 'lg' = '';
  @Prop() icon?: string;
  @Prop() label?: string;
  @Prop() type: 'button' | 'submit' | 'reset' = 'button';
  @Prop() form?: string;
  @Prop() rounded?: boolean;
  @Prop() tooltip?: string;
  @Prop() buttonData?: Record<string, string>;
  @Prop() class: Record<string, boolean> | string;

  @Event() buttonClick: EventEmitter;

  private readonly handleClick = (e: Event) => {
    if (this.type === 'button') {
      e.stopPropagation();
      this.buttonClick.emit();
    }
  };

  iconRender() {
    if (!this.icon) return;
    return (
      <span class="btn-inner--icon">
        <FnIcon>{this.icon}</FnIcon>
      </span>
    );
  }

  labelRender() {
    if (!this.label) return;
    return (
      <span
        class={{
          'btn-inner--text': !!this.icon,
          'storm-button-label': true,
        }}
      >
        {this.label}
      </span>
    );
  }

  buttonRender() {
    const classes: Record<string, boolean> = mergeClasses(
      {
        'm-1': true,
        btn: true,
        active: this.active === true,
        disabled: this.disabled === true,
        'btn-icon-only': !!this.icon && !this.label,
        'btn-rounded': this.rounded === true,
        'd-inline-flex': true,
        'align-items-center': true,
        'justify-content-center': true,
      },
      parseClassNames(this.class),
    );
    if (this.size) classes['btn-' + this.size] = true;
    if (this.buttonStyle && this.buttonStyle.startsWith('gradient-')) classes['bg-' + this.buttonStyle] = true;
    else classes['btn-' + (this.buttonStyle ?? 'primary')] = true;
    return (
      <button type={this.type} class={classes} form={this.form} onClick={this.handleClick}>
        {this.iconRender()}
        {this.labelRender()}
        <slot></slot>
      </button>
    );
  }

  render() {
    if (this.tooltip) {
      return <storm-tooltip heading={this.tooltip}>{this.buttonRender()}</storm-tooltip>;
    } else {
      return this.buttonRender();
    }
  }
}
